import React, { Component } from 'react'
import QrReader from 'react-qr-reader'
import { makeStyles } from '@material-ui/core'


const useStyles = makeStyles({
    appPlanet: {
      paddingLeft: '10px',
      paddingRight: '10px',
      justifyContent:"left",
      alignItems: "left:",
      color: "#000000",

      width: '50%',
      height: '200px'
    }
  })

class QR extends Component {

    constructor(props) {
        super(props);
        this.state = {
            result: 'No result'
        };
      }

    handleScan = data => {
      if (data) {
        this.setState({
          result: data
        })
      }
    }
    handleError = err => {
      console.error(err)
    }
    render() {
      return (
        <div>
          <QrReader
            delay={300}
            onError={this.handleError}
            onScan={this.handleScan}
            style={{ 
                width: '100%',
                alignItems: 'center',
                paddingLeft: '0px',
                paddingRight: '0px',
            }}
          />
          <p>{this.state.result}</p>
        </div>
      )
    }
  }
export default QR;